import Navigation from './components/Navigation/Navigation.jsx';
import style from './App.module.scss';
import Header from './components/Header/Header.jsx';
import Messages from "./components/Main/Messages/Mesagges.jsx";
import Profile from "./components/Main/Profile/Profile.jsx"
import Users from "./components/Main/Users/Users.jsx"
import { Route } from 'react-router-dom';
import { useState } from 'react';
import LoginForm from './components/Main/LoginForm/LoginForm.jsx';


function App() {


  const [state, setState] = useState(false)

  return (

    <div className={state ? style.app__wrapper + ' ' + style.app__wrapper_show : style.app__wrapper}>
      <Header state={state} setState={setState} />
      <div className={state ? style.mainWrapper + ' ' + style.mainWrapper_show : style.mainWrapper}>
        <Navigation setState={setState} state={state} />
        <div className={style.app__container}>
          <Route path='/profile' component={() => <Profile />} />
          <Route path='/messages' component={() => <Messages />} />
          <Route path='/users' component={() => <Users />} />
          <Route path='/login' component={() => <LoginForm />} />
        </div>
      </div>

    </div>
  );
}

export default App;
