import style from './Header.module.scss'
import { HiOutlineMenu } from "react-icons/hi"

const Header = ({ setState, state }) => {





   return (
      <header className={style.header}>
         <div className={state ? style.header__wrapper + ' ' + style.header__wrapper_show : style.header__wrapper}>
            <button onClick={() => setState(!state)} className={style.header__button}><HiOutlineMenu className={style.header__image} /></button>
            <h2 className={style.header__title}>React Social Club</h2>
            <div className={style.header__login}></div>
         </div>
      </header>
   )
}
export default Header;
