import { NavLink } from 'react-router-dom'
import style from './Navigation.module.scss'
import { AiOutlineUsergroupAdd } from 'react-icons/ai'
import { CgProfile } from 'react-icons/cg'
import { FiMessageSquare } from 'react-icons/fi'


const Navigation = ({ state, setState }) => {


   return (
      <div className={state ? style.overlay : null} >

         <div className={state ? style.navigation__wrapper + ' ' + style.navigation__wrapper_show : style.navigation__wrapper}>
            <nav className={style.navigation__nav}>
               <NavLink onClick={() => setState(false)} activeClassName={style.navigation__active} to={'/profile'}> <div className={style.navigation__container} >
                  <CgProfile className={style.navigation__image} />
                  <li className={style.navigation__item}> Profile</li>
               </div></NavLink>
               <NavLink onClick={() => setState(false)} activeClassName={style.navigation__active} to={'/users'}> <div className={style.navigation__container} >
                  <AiOutlineUsergroupAdd className={style.navigation__image} />
                  <li className={style.navigation__item}> Users</li>
               </div></NavLink>
               <NavLink onClick={() => setState(false)} activeClassName={style.navigation__active} to={'/messages'}>  <div className={style.navigation__container}  >
                  <FiMessageSquare className={style.navigation__image} />
                  <li className={style.navigation__item}> Messages</li>
               </div></NavLink>
               <NavLink onClick={() => setState(false)} activeClassName={style.navigation__active} to={'/login'}>  <div className={style.navigation__container}  >
                  <FiMessageSquare className={style.navigation__image} />
                  <li className={style.navigation__item}> Login</li>
               </div></NavLink>


            </nav>

         </div>
      </div >
   )
}
export default Navigation;