import style from './Messages.module.scss'

const Messages = () => {
   return <div>
      <h2 className={style.message__title}>Messages</h2>
      <blockquote>Всегда пишите код так, будто сопровождать его будет склонный к насилию психопат, который знает, где вы живете.
      </blockquote>
      <cite className={style.message__author}>Martin Golding</cite>
   </div>

}
export default Messages;